<template>
  <div>
    <div class="mdui-bottom-nav mdui-color-blue mdui-text-color-white">
      <a @click="tabBarDidActived(0)" href="javascript:void(0);" class="mdui-ripple mdui-ripple-white mdui-bottom-nav-active">
        <i class="mdui-icon material-icons">&#xe0b7;</i>
        <label>消息</label>
      </a>
      <a @click="tabBarDidActived(1)" href="javascript:void(0);" class="mdui-ripple mdui-ripple-white">
        <i class="mdui-icon material-icons">&#xe851;</i>
        <label>好友</label>
      </a>
      <a @click="tabBarDidActived(2)" href="javascript:void(0);" class="mdui-ripple mdui-ripple-white">
        <i class="mdui-icon material-icons">&#xe853;</i>
        <label>个人中心</label>
      </a>
    </div>
    <message-list v-if="activedTabBarIndex === 0"></message-list>
    <friend-list v-if="activedTabBarIndex === 1"></friend-list>
    <mine v-if="activedTabBarIndex === 2"></mine>
  </div>
</template>

<script>
import MessageList from '../components/message-list'
import FriendList from '../components/friend-list'
import Mine from '../components/mine'
export default {
  name: 'views-home',
  components: {Mine, FriendList, MessageList},
  data () {
    return {
      activedTabBarIndex: 0
    }
  },
  methods: {
    tabBarDidActived (index) {
      this.activedTabBarIndex = index
    }
  }
}
</script>

<style scoped>
.mdui-bottom-nav {
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
}
</style>



