<template>
  <div>
    <h4>当前用户: {{ info.user.nickname }}</h4>
    <div class="mdui-btn-block mdui-btn" @click="logout">重新登录</div>
  </div>
</template>

<script>
  import Tools from '../sdk/Tools'
  export default {
    name: 'mine',
    mounted () {
    },
    computed: {
      info () {
        return Tools.userGet()
      }
    },
    methods: {
      logout () {
        this.$_WebIM_conn.close();
        this.$router.push('/login')
      }
    }
  }
</script>

<style scoped>

</style>



