<template>
  <div>
    <ul class="mdui-list">
      <li v-for="(item, index) in lists" :key="index" @click="userSelected(item)" class="mdui-list-item mdui-ripple">
        <div class="mdui-list-item-avatar"><img src="../assets/temp/avatar1.jpg"/></div>
        <div class="mdui-list-item-content">{{ item.name }}</div>
      </li>
    </ul>
  </div>
</template>

<script>
  import Tools from '../sdk/Tools'
  export default {
    name: 'friend-list',
    data () {
      return {
        lists: []
      }
    },
    mounted () {
      this.loadFriends()
    },
    computed: {
      user () {
        return Tools.userGet().user
      }
    },
    methods: {
      loadFriends () {
        console.log(888)
        let _this = this
        this.$_WebIM_conn.getRoster({
          success (roster) {
            let array = []
            for (let i = 0; i < roster.length; i++) {
              if ( roster[i].subscription === 'both' || roster[i].subscription === 'to') {
                array.push(roster[i])
              }
            }
            _this.lists = array
            console.log(roster)
          },
          error (e) {
            console.log(e)
          }
        })
      },
      userSelected (item) {
        this.$router.push('/talk/'+ item.name)
      }
    }
  }
</script>

<style scoped>

</style>



